.choosecardOuter{
    box-sizing: border-box;
    margin: 30px;
    padding: 30px;
    display: flex;
width: 350px;
height: 350px;
flex-direction: column;
align-items: start;
border-radius: 14px;
border: 1.4px solid rgba(255, 255, 255, 0.10);
background: #1D1D1D;
}
.choosecardImg{
    width: 101px;
height: 101px;
flex-shrink: 0;
border-radius: 101px;
border: 1.4px solid rgba(25, 25, 25, 0.10);
background: var(--pure-white, #FFF);
}
.successRate{
    color: var(--pure-white, #FFF);
font-size: 24px;
font-family: Inter;
font-style: normal;
font-weight: 600;
line-height: 24px;
}
.chooseCardPara{
   text-align: left;
    color: var(--pure-white, #FFF);
font-size: 14px;
font-family: Inter;
font-style: normal;
font-weight: 500;
line-height: 162.523%;
opacity: 0.30000001192092896;

}
.choosecardButton{
    width: 121px;
height: 43px;
flex-shrink: 0;
border-radius: 23.5px;
background: #E3B748;
}